  width: 100%;
}

:where(.css-dev-only-do-not-override-1c0na6j).ant-table-wrapper .ant-table-thead >tr>th, :where(.css-dev-only-do-not-override-1c0na6j).ant-table-wrapper .ant-table-thead >tr>td {
  position: relative;
  color: rgba(0, 0, 0, 0.88);
  font-weight: 600;
  text-align: start;
  background: #0185f1;
  border-bottom: 1px solid #f0f0f0;

}

.blinking-dot {
  height: 5px;
  width: 5px;
  background-color: rgb(7, 224, 7);
  border-radius: 50%;
  display: inline-block;
  margin-left: 5px;
  animation: blinking 1s infinite;
}

.blinking-tool-icon {
  color: rgb(6, 218, 6);
  animation: blinking 1s infinite;
}

.blinking-car-icon {
  color: orange;
  animation: blinking 1s infinite;
}

.ant-checkbox-wrapper {
